import React, { useState } from "react";
import style from "./ResetPassword.module.css";
import { useFormik } from "formik";
import * as Yup from 'yup'
import axios from "axios";
import { useNavigate } from "react-router-dom";
export default function ResetPassword() {
const baseUrl = 'https://ecommerce.routemisr.com'
const [error, seterror] = useState(null)
const [loading, setloading] = useState(false)
let nav = useNavigate()
let validationSchema = Yup.object({
  email:Yup.string().required("Email required").email(),
  newPassword:Yup.string().required("newPassword required").matches(/^[A-Z][a-z0-9]{5,10}$/,"password must start with uppercase")
})

 let updataPassword = useFormik({
  initialValues:{
    email:"",
    newPassword:""
  },
  onSubmit:updatapasswordsubmit,
  validationSchema
 }) 
 async function updatapasswordsubmit (values){
  setloading(true)
  let {data} = await axios.put(`${baseUrl}/api/v1/auth/resetPassword`,values).catch((err)=>{
    seterror(err.response.data.message);
    setloading(false)
  })
  if(data.token){
    setloading(false)
    nav('/login')
  }
 }
  return (
    <>
    <div className="py-5">
      <h2 className="mb-2">Reset Password</h2>
      {error?<div className="alert alert-danger">{error}</div>:null}
      <form onSubmit={updataPassword.handleSubmit}>
        <div>
          <label htmlFor="email">Enter email:</label>
          <input onChange={updataPassword.handleChange}
          onBlur={updataPassword.handleBlur}
          value={updataPassword.values.email}
           type="email" name="email" id="email" className="form-control" />
        </div>
        {updataPassword.touched.email && updataPassword.errors.email?<p className="text-danger">{updataPassword.errors.email}</p>:""}
        <div>
          <label htmlFor="newPassword">Enter new password:</label>
          <input onChange={updataPassword.handleChange}
          onBlur={updataPassword.handleBlur}
          value={updataPassword.values.newPassword}
           type="password" name="newPassword" id="newPassword" className="form-control" />
        </div>
        {updataPassword.touched.newPassword && updataPassword.errors.newPassword?<p className="text-danger">{updataPassword.errors.newPassword}</p>:""}
        {loading?<button className="btn btn-success d-block ms-auto mt-3"><i className="fa-solid fa-spinner fa-spin"></i></button>:
         <button className="btn btn-success d-block ms-auto mt-3">Updata Password</button>}
      </form>
    </div>
      
    </>
  );
}
