import React,{ useContext, useState } from "react";
import style from "./Login.module.css";
import { Formik, useFormik } from "formik";
import * as Yup from 'yup'
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { BallTriangle } from "react-loader-spinner";
import { UserContext } from "../../Context/userContext";


export default function Login() {
  let {setuserToken} = useContext(UserContext)
  const baseUrl = 'https://ecommerce.routemisr.com'
  let nav = useNavigate()
  let [error,setError]=useState(null)
  const [isLoading, setisLoading] = useState(false);
  let validationSchema = Yup.object({  
    email:Yup.string().required("email required").email("emial is invalid"),   
    password:Yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/,"password must start with uppercase").required("password required"), 
  })
  let loginForm = useFormik({
    initialValues:{  
      email:"",
      password:""   
    },
    onSubmit:submitlogin,
    validationSchema
  })
  async function submitlogin (values){
    setisLoading(true)
    let {data} = await axios.post(`${baseUrl}/api/v1/auth/signin`,values).catch((err)=>{
      setError(err.response.data.message);
      setisLoading(false)
    })
    if(data.message=='success'){
      setisLoading(false)
      localStorage.setItem("userToken",data.token)
      setuserToken(data.token)
      nav('/')
    }
  }
  return (
    <>
     <div className="py-3">
      <h2 className="pb-2">login Now:</h2>
      {error? <div className="alert alert-danger">{error}</div>:null}     
      <form onSubmit={loginForm.handleSubmit} >
        <div className="my-2">
          <label htmlFor="email">Enter Email:</label>
          <input onChange={loginForm.handleChange}
                 onBlur={loginForm.handleBlur}
                 value={loginForm.values.email}
           type="email" name="email" id="email" className="form-control" />
        </div>
        {loginForm.errors.email && loginForm.touched.email ?  <p className="text-danger">{loginForm.errors.email}</p>:""}
        <div className="my-2">
          <label htmlFor="password">Enter Password:</label>
          <input onChange={loginForm.handleChange}
                 onBlur={loginForm.handleBlur}
                 value={loginForm.values.password}
          type="password" name="password" id="password" className="form-control" />
        </div>
        {loginForm.errors.password && loginForm.touched.password ?  <p className="text-danger">{loginForm.errors.password}</p>:""}
       
        {isLoading?<button  type="button" className="btn btn-success d-block ms-auto"><BallTriangle  height={25} width={100} radius={5} color="#fff" ariaLabel="ball-triangle-loading" wrapperClass={{}} wrapperStyle="" visible={true}/></button>
        : <button disabled={!(loginForm.isValid && loginForm.dirty)} type="submit" className="btn btn-success d-block ms-auto">Login</button>}
        <div className="d-flex justify-content-between pt-3">
 <Link to='/register' className="nav-link border-bottom border-2">Register Now?</Link>
 <Link to='/forgetpassword'>forget password?</Link>
        </div>
      </form>
    </div>     
    </>
  );
}
