import React, { useState } from "react";
import style from "./Register.module.css";
import { Formik, useFormik } from "formik";
import * as Yup from 'yup'
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { FallingLines } from "react-loader-spinner";

export default function Register() {
  const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
  const baseUrl = 'https://ecommerce.routemisr.com'
  let nav = useNavigate()
  let [error,setError]=useState(null)
  const [isLoading, setisLoading] = useState(false);
  let validationSchema = Yup.object({
    name:Yup.string().required("name required").min(3,"name min lenght is 3").max(20,"name max lenght is 20"),
    email:Yup.string().required("email required").email("emial is invalid"),
    phone:Yup.string().matches(phoneRegExp,"phone number is invalid").required("phone is required"),
    password:Yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/,"password must start with uppercase").required("password required"),
    rePassword:Yup.string().required("confirm password required").oneOf([Yup.ref("password")],"password and confirm password not match")
  })
  let registerForm = useFormik({
    initialValues:{
      name:"",
      email:"",
      password:"",
      rePassword:"",
      phone:""
    },
    onSubmit:submitRegister,
    validationSchema
  })
  async function submitRegister (values){
    setisLoading(true)
    let {data} = await axios.post(`${baseUrl}/api/v1/auth/signup`,values).catch((err)=>{
      setError(err.response.data.message);
      setisLoading(false)
    })
    if(data.message=='success'){
      setisLoading(false)
      nav('/login')
    }
  }
  return (
    <>
    <div className="py-3">
      <h2 className="pb-2">Register Now:</h2>
      {error? <div className="alert alert-danger">{error}</div>:null}     
      <form onSubmit={registerForm.handleSubmit} >
        <div className="my-2">
          <label htmlFor="name">Enter Name:</label>
          <input onChange={registerForm.handleChange}
                 onBlur={registerForm.handleBlur}
                 value={registerForm.values.name}
           type="text" name="name" id="name" className="form-control" />
        </div>     
        {registerForm.errors.name && registerForm.touched.name ?  <p className="text-danger">{registerForm.errors.name}</p>:""}
        <div className="my-2">
          <label htmlFor="email">Enter Email:</label>
          <input onChange={registerForm.handleChange}
                 onBlur={registerForm.handleBlur}
                 value={registerForm.values.email}
           type="email" name="email" id="email" className="form-control" />
        </div>
        {registerForm.errors.email && registerForm.touched.email ?  <p className="text-danger">{registerForm.errors.email}</p>:""}
        <div className="my-2">
          <label htmlFor="password">Enter Password:</label>
          <input onChange={registerForm.handleChange}
                 onBlur={registerForm.handleBlur}
                 value={registerForm.values.password}
          type="password" name="password" id="password" className="form-control" />
        </div>
        {registerForm.errors.password && registerForm.touched.password ?  <p className="text-danger">{registerForm.errors.password}</p>:""}
        <div className="my-2">
          <label htmlFor="rePassword">Confirm Password:</label>
          <input onChange={registerForm.handleChange}
                 onBlur={registerForm.handleBlur}
                 value={registerForm.values.rePassword}
           type="password" name="rePassword" id="rePassword" className="form-control" />
        </div>
        {registerForm.errors.rePassword && registerForm.touched.rePassword ?  <p className="text-danger">{registerForm.errors.rePassword}</p>:""}
        <div className="my-2">
          <label htmlFor="phone">Enter Phone:</label>
          <input onChange={registerForm.handleChange}
                 onBlur={registerForm.handleBlur}
                 value={registerForm.values.phone}
           type="tel" name="phone" id="phone" className="form-control" />
        </div>
        {registerForm.errors.phone && registerForm.touched.phone ?  <p className="text-danger">{registerForm.errors.phone}</p>:""}
        {isLoading?<button  type="button" className="btn btn-success d-block ms-auto"><FallingLines color="#fff" width="25" visible={true}  ariaLabel='falling-lines-loading'/></button>:
        <button disabled={!(registerForm.isValid && registerForm.dirty)} type="submit" className="btn btn-success d-block ms-auto">Register</button>}     
      </form>
    </div>     
    </>
  );
}
