import React, { useContext, useEffect, useState } from "react";
import style from "./WishList.module.css";
import { WishList } from './../../Context/WishListContext';
import { Hearts } from "react-loader-spinner";
import { CartContext } from "../../Context/CartContext";
import toast from "react-hot-toast";
export default function WishListComponent() {
  let {getLoggedUserWishList,removeProductFromWishList} = useContext(WishList)
  let {addToCart,setcartCount} =useContext(CartContext)
  const [productWishList, setproductWishList] = useState(null)
  const [loading, setloading] = useState(false)
  async function getData (){
    setloading(true)
    let {data} = await getLoggedUserWishList()
    setproductWishList(data)
    setloading(false)
  }
  async function removeProduct (id){
    setloading(true)
    let {data} = await removeProductFromWishList(id)
    if(data.status=="success"){
      getData()
    }
    setloading(false)
  }
  async function addProductToCart (id){
    let {data} = await addToCart(id)
    if(data.status=="success"){
      setcartCount(data.numOfCartItems)
      toast.success(data.message,{
        duration:2000
      })
    }else{
      toast.error(data.status)
    }
  }
  useEffect(()=>{
    getData()
  },[])
  return (<>
  {loading? <div className="position-fixed top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
  <Hearts 
  height="80"
  width="80"
  color="#4fa94d"
  ariaLabel="hearts-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
/>
</div>:""}
  {productWishList? <div className="mt-5 bg-main-light p-3">
    <div className="py-5 border-bottom">
    <h2 className=" h3   fw-bolder">My Wish List:</h2>
    <h3 className="h5   fw-bold text-main mt-3">Numper of Items: {productWishList.count}</h3>
  </div>
  {productWishList.data.map((product)=>{
    return <div key={product._id} className="row align-items-center p-2  border-bottom">
      <div className="col-md-2">
        <img src={product.imageCover} className="w-100" alt={product.title} />
      </div>
      <div className="col-md-10">
        <div className="d-flex justify-content-between">
          <div>
            <h4 className="h6 fw-bold">{product.title}</h4>
            <p className="text-main fw-bolder">{product.price} EGP</p>
            <button onClick={()=>removeProduct(product._id)} className="btn btn-sm btn-danger"><i className="fa-solid fa-trash"></i>Remove</button>
          </div>
          <div>
            <button onClick={()=>addProductToCart(product._id)} className="btn bg-main ">Add To Cart</button>
          </div>
        </div>
      </div>
    </div>
  })}
  </div>: <div className="py-5 p-3">
    <div className="py-5 bg-main-light">
       <h3 className="fw-bolder ps-2">My Wish List</h3>
       </div>
  </div> }

    </>
  );
}
