import React, { useContext } from "react";
import style from "./Navbar.module.css";
import { Link, useNavigate } from "react-router-dom";
import logo from '../../assets/images/freshcart-logo.svg'
import { UserContext } from "../../Context/userContext";
import { CartContext } from "../../Context/CartContext";

export default function Navbar() {
  let {userToken,setuserToken}=useContext(UserContext)
  let {cartCount} = useContext(CartContext)
  let nav = useNavigate()

  function logOut (){
    localStorage.removeItem("userToken")
    setuserToken(null)
    nav("/login")
  }
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top">
        <div className="container ">
          <Link to='/' className="navbar-brand" >
            <img src={logo} alt="" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              {userToken!=null?<>            <li className="nav-item">
                <Link to='/' className="nav-link" >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link to='cart' className="nav-link" >
                  Cart
                </Link>
              </li>
              <li className="nav-item">
                <Link to='wishlist' className="nav-link" >
                  Wish List
                </Link>
              </li>
              <li className="nav-item">
                <Link to='products' className="nav-link">
                Products
                </Link>
              </li>
              <li className="nav-item">
                <Link to='category' className="nav-link" >
                Category
                </Link>
              </li>
              <li className="nav-item">
                <Link to='brands' className="nav-link">
                Brands
                </Link>
              </li></>:""}
            </ul>
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              {userToken==null?<>              <li className="nav-item">
                <Link to='login' className="nav-link" >
                Login
                </Link>
              </li>
              <li className="nav-item">
                <Link to='register' className="nav-link" >
                Register
                </Link>
              </li></>:<> <Link to='cart'> <li className="nav-item d-flex align-items-center position-relative p-1">
               <i className="fa-solid fa-cart-shopping fa-2x me-2 cursor-pointer"></i>
               <div className="badge position-absolute top-0 end-0 text-white bg-main">
                {cartCount}
               </div>
              </li></Link>
              <li className="nav-item">
              <span onClick={logOut}  className="nav-link cursor-pointer" >
              Log Out
              </span>
            </li></>
              }          
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
