import React, { useState } from "react";
import style from "./ForgetPassword.module.css";
import { useFormik } from "formik";
import * as Yup from 'yup'
import axios from "axios";
import { useNavigate } from "react-router-dom";
export default function ForgetPassword() {
  const baseUrl = 'https://ecommerce.routemisr.com'
  const [errorpass, seterrorpass] = useState(null)
  const [errorcode, seterrorcode] = useState(null)
  const [loading, setloading] = useState(false)
  let nav = useNavigate()
  let validationSchema = Yup.object({
    email:Yup.string().required("email required to send code").email("Enter valid email")
  })
  let resetPassword = useFormik({
    initialValues:{
      email:""
    },
    onSubmit:sendCodeToResetPassword,
    validationSchema  
  })
  async function sendCodeToResetPassword (val){
    setloading(true)
   let {data} = await axios.post(`${baseUrl}/api/v1/auth/forgotPasswords`,val).catch((err)=>{
    seterrorpass(err.response.data.message);
    setloading(false)
   })
   if(data.statusMsg=='success'){
    setloading(false)
    document.getElementById("toCode").classList.remove("d-none")
    document.getElementById("forgetpassword").classList.add("d-none")
   }
  }

let validationSchema2 = Yup.object({
  resetCode:Yup.string().required("code required")
})
  let verifyCode = useFormik({
    initialValues:{
      resetCode:""
    },
    onSubmit:verifyCodeSubmit,
    validationSchema:validationSchema2
  })
async function verifyCodeSubmit (val){
  setloading(true)
  let {data} = await axios.post(`${baseUrl}/api/v1/auth/verifyResetCode`,val).catch((err)=>{
    seterrorcode(err.response.data.message);
    setloading(false)
  })
  if(data.status=='Success'){
    setloading(false)
    nav('/resetpassword')
  }
}
  return (
    <>
     <div className="py-5" id='forgetpassword'>
      <h2 className="pb-2">Reset Password</h2>
      {errorpass? <div className="alert alert-danger">{errorpass}</div>:null}
      <form onSubmit={resetPassword.handleSubmit}>
        <div>
          <label htmlFor="email">Enter Your Email</label>
          <input onChange={resetPassword.handleChange}
                 onBlur={resetPassword.handleBlur}
                 value={resetPassword.values.email}
          type="email" name="email" id="email" className="form-control" />
        </div>
        {resetPassword.errors.email && resetPassword.touched.email?<p className="text-danger">{resetPassword.errors.email}</p>:""} 
        {loading? <button type="submit" className="btn btn-success d-block ms-auto mt-3"><i className="fa-solid fa-spinner fa-spin"></i></button>: <button type="submit" className="btn btn-success d-block ms-auto mt-3">Send Code</button>}             
      </form>
     </div>
     <div id='toCode' className="d-none py-5">
      <form onSubmit={verifyCode.handleSubmit} >
      {errorcode? <div className="alert alert-danger">{errorcode}</div>:null}
        <label htmlFor="resetCode">Enter Code:</label>
        <input onChange={verifyCode.handleChange}
        onBlur={verifyCode.handleBlur}
        value={verifyCode.values.resetCode}
        type="text" name="resetCode" id="resetCode" className="form-control" />
       {verifyCode.touched.resetCode && verifyCode.errors.resetCode ? <p className="text-danger">{verifyCode.errors.resetCode}</p>:""}
       {loading?<button type="submit" className="btn btn-success d-block ms-auto mt-3"><i className="fa-solid fa-spinner fa-spin"></i></button>:
       <button type="submit" className="btn btn-success d-block ms-auto mt-3">Verify</button> }      
      </form>
     </div>
    </>
  );
}
