import './App.css';
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import Layout from './Components/Layout/Layout';
import Home from './Components/Home/Home';
import Products from './Components/Products/Products';
import Cart from './Components/Cart/Cart';
import Brands from './Components/Brands/Brands';
import Category from './Components/Category/Category';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import Notfound from './Components/Notfound/Notfound';
import { useContext, useEffect } from 'react';
import { UserContext } from './Context/userContext';
import ProtectRoute from './Components/ProtectRoute/ProtectRoute';
import ForgetPassword from './Components/ForgetPassword/ForgetPassword';
import ResetPassword from './Components/ResetPassword/ResetPassword';
import ProductsDetailes from './Components/productsDetailes/productsDetailes';
import CartContextProvider from './Context/CartContext';
import  { Toaster } from 'react-hot-toast';
import WishListProvider from './Context/WishListContext';
import WishListComponent from './Components/WishList/WishList';
import CheckOut from './Components/CheckOut/CheckOut';
import AllOrders from './Components/AllOrders/AllOrders';


function App() {
  let routes = createHashRouter([
    {path:'/',element:<Layout/>,children:[
      {index:true,element:<ProtectRoute><Home/></ProtectRoute>},
      {path:'register',element:<Register/>},
      {path:'login',element:<Login/>},
      {path:'forgetpassword',element:<ForgetPassword/>},
      {path:'resetpassword',element:<ResetPassword/>},
      {path:'products',element:<ProtectRoute><Products/></ProtectRoute>},
      {path:'Productdetailes/:id',element:<ProtectRoute><ProductsDetailes/></ProtectRoute>},
      {path:'category',element:<ProtectRoute><Category/></ProtectRoute>},
      {path:'brands',element:<ProtectRoute><Brands/></ProtectRoute>},  
      {path:'cart',element:<ProtectRoute><Cart/></ProtectRoute>},
      {path:'wishlist',element:<ProtectRoute><WishListComponent/></ProtectRoute>},
      {path:'checkout/:id',element:<ProtectRoute><CheckOut/></ProtectRoute>},
      {path:'allorders',element:<ProtectRoute><AllOrders/></ProtectRoute>},
      {path:'*',element:<Notfound/>},
    ]},
  ])
  let {setuserToken} = useContext(UserContext)
  useEffect(()=>{
      if(localStorage.getItem("userToken"))
      {
      setuserToken(localStorage.getItem("userToken"))
      }
},[])  

return <>   <WishListProvider>
              <CartContextProvider>
              <RouterProvider router={routes}/>
              <Toaster/>
             </CartContextProvider>
          </WishListProvider>
  </>
}
export default App;
