import React, { useContext, useState } from "react";
import style from "./CheckOut.module.css";
import { useFormik } from "formik";
import * as Yup from 'yup'
import { ThreeDots } from "react-loader-spinner";
import { CartContext } from "../../Context/CartContext";
import { useParams } from "react-router-dom";
export default function CheckOut() {
  let {checkPayment} = useContext(CartContext)
  const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
  const [isloading, setisloading] = useState(false)
  let validationSchema = Yup.object({
    details:Yup.string().required("Details required"),
    phone:Yup.string().matches(phoneRegExp,"phone number is invalid").required("phone is required"),
    city:Yup.string().required("city required")
  })
  let shippingform = useFormik({
    initialValues:{
      details:"",
      phone:"",
      city:""
    },
    validationSchema,
    onSubmit:shippingsubmit
  })
  let {id} = useParams()
 async function shippingsubmit (val){
    setisloading(true)
    let {data} = await checkPayment(id,val)
    if(data.status=="success"){
      setisloading(false)
      window.location.href=data.session.url
    }
  }
  return (
    <>
    <div className="py-5">
      <form onSubmit={shippingform.handleSubmit} >
        <div className="pb-2">
          <label htmlFor="details">Details</label>
          <input onChange={shippingform.handleChange}
                 onBlur={shippingform.handleBlur}
                 value={shippingform.values.details}
           className="form-control" type="text" name="details" id="details" />
        </div>
        {shippingform.touched.details && shippingform.errors.details ?<p className="text-danger">{shippingform.errors.details}</p>:""}
        <div className="pb-2">
          <label htmlFor="phone">Phone</label>
          <input onChange={shippingform.handleChange}
                 onBlur={shippingform.handleBlur}
                 value={shippingform.values.phone}
          className="form-control" type="tel" name="phone" id="phone" />
        </div>
        {shippingform.touched.phone && shippingform.errors.phone ?<p className="text-danger">{shippingform.errors.phone}</p>:""}
        <div className="pb-2">
          <label htmlFor="city">City</label>
          <input onChange={shippingform.handleChange}
                 onBlur={shippingform.handleBlur}
                 value={shippingform.values.city}
          className="form-control"  type="text" name="city" id="city" />
        </div>
        {shippingform.touched.city && shippingform.errors.city ?<p className="text-danger">{shippingform.errors.city}</p>:""}
        {isloading?<button  className="btn btn-outline-info d-bloke w-100 mt-3 d-flex justify-content-center"><ThreeDots height="25" width="80" radius="9" color="#4fa94d" ariaLabel="three-dots-loading" wrapperStyle={{}}wrapperClassName="" visible={true} /> </button>:
        <button disabled={!(shippingform.isValid && shippingform.dirty)} className="btn btn-outline-info d-bloke w-100 mt-3 ">Pay Now <i className="fa-brands fa-cc-visa"></i></button>
        }
      </form>
    </div>
    </>
  );
}
