import React, { useContext, useEffect, useState } from "react";
import style from "./Cart.module.css";
import { CartContext } from "../../Context/CartContext";
import { Circles } from "react-loader-spinner";
import { Link } from "react-router-dom";
export default function Cart() {
  let {getLoggedUserCart,deleteSpecificCartItem,clearUserCart,updataCartProductQuantity,setcartCount} = useContext(CartContext)
  const [cartproduct, setcartproduct] = useState(null)
  const [loading, setloading] = useState(false)
  async function getCart (){
    setloading(true)
    let {data} = await getLoggedUserCart()
    setcartproduct(data);
    setloading(false)
  }
  async function deleteItem(id){
    setloading(true)
   let {data} = await deleteSpecificCartItem(id)
   setcartproduct(data);
   setcartCount(data.numOfCartItems)
   setloading(false)
  }
 async function clearCart (){
  setloading(true)
    let {data} = await clearUserCart()
   if(data.message="success"){
  setcartproduct(null)
  setloading(false)
  }
  }
  async function updataCount (id,count){
    setloading(true)
    let {data} = await updataCartProductQuantity(id,count);
    setcartproduct(data); 
    setloading(false)
  }
  useEffect(()=>{
    getCart()
  },[])
  return (
    <>
     {loading?    <div className="loading z-3 position-fixed top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
    <Circles
    height="80"
    width="80"
    color="#4fa94d"
    ariaLabel="circles-loading"
    wrapperStyle={{}}
   wrapperClass=""
    visible={true}
   />
    </div>:null}
    {cartproduct?  <div className="py-2">
   <div className="p-3 bg-main-light ">
   <div className="mb-3 border-bottom d-flex justify-content-between">
     <div>
     <h2>Shop Cart:</h2>
      <h3 className="h5 text-main fw-bolder">Cart Items:{cartproduct.numOfCartItems}</h3>
      <h3 className="h6 text-main fw-bolder">Total Cart Price:{cartproduct.data.totalCartPrice} EGP</h3>
    </div>
    <div className="align-self-center">
      <Link to={'/checkout/' + cartproduct?.data._id }>
        <span className="btn btn-success btn-lg">Check Out</span>
      </Link>
    </div>
   </div>
     {cartproduct.data.products.map((product)=>{
      return <div key={product.product._id} className="row p-2 border-bottom ">
        <div className="col-md-1">
          <img src={product.product.imageCover} className="w-100" alt={product.product.title} />
        </div>
        <div className="col-md-11">
          <div className="d-flex justify-content-between align-items-center">
            <div>
            <h2 className="h6">{product.product.title.split(" ").slice(0,3).join(" ")}</h2>
            <h3 className="text-main h6 fw-bold">price:{product.price} EGP</h3>
            <button onClick={()=>deleteItem(product.product._id)} className="btn btn-danger btn-sm "><i className="fa-solid fa-trash-can"></i>Remove</button>
            </div>
            <div>
            <i onClick={()=>updataCount(product.product._id,product.count + 1)} className="fa-solid fa-plus text-main p-2 cursor-pointer border rounded"></i>
                <span className="p-2">{product.count}</span>
            <i onClick={()=>updataCount(product.product._id,product.count - 1)} className="fa-solid fa-minus text-main p-2 cursor-pointer border rounded "></i>
            </div>
          </div> 
        </div>
      </div>
     })}
  <button onClick={()=>clearCart()} className="btn btn-success d-block mx-auto mt-2 p-2 w-50">Clear Cart</button>
    </div>
   </div>:<div className="bg-main-light py-5 my-5 p-5 container">
    <div className="d-flex justify-content-between mb-4">
      <h2>Cart Shop</h2>
    </div>
    <p className="h4">Your cart is empty</p>
   </div>}
 
    </>
  );
}
