import React from "react";
import style from "./Footer.module.css";
export default function Footer() {
  return (
    <>
    <div className="bg-main-light py-4 ">
      <div className="container">
        <h3 className="h2 ">Get the freshCart app</h3>
        <p>we will send you a link,open it in your phone to download the app </p>
        <div className="row ps-4 border-bottom pb-3">
          <div className="col-md-10">
            <input type="email" className="form-control" placeholder="Email"  />
          </div>
          <div className="col-md-2">
            <button className="btn btn-success">Share App Link</button>
          </div>
        </div>
        <div className="d-flex justify-content-between py-3 border-bottom">
          <div>
            <h2 className="d-inline fw-bold h6">Payment partners</h2>
            <i className="fa-brands  p-2 fa-amazon-pay"></i> 
            <i className="fa-brands  p-2 fa-cc-paypal"></i>
            <i className="fa-brands  p-2 fa-cc-mastercard"></i>    
           </div>
          <div className="small">
            <h2 className="d-inline fw-bold h6">Get deliveries with freshCart</h2>
            <i className="fa-brands  p-2 fa-google-play "></i>
            <i className="fa-brands  p-2 fa-app-store-ios"></i>
          </div>
        </div>

      </div>
    </div>
    </>
  );
}
