import React, { useContext, useEffect, useState } from "react";
import style from "./FeaturedProduct.module.css";
import axios from "axios";
import { useQuery } from "react-query";
import { ColorRing } from "react-loader-spinner";
import { Link } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
import toast from "react-hot-toast";
import { WishList } from "../../Context/WishListContext";
import $ from 'jquery'
export default function FeaturedProduct() {


  useEffect(()=>{
    $('.fa-heart').click(function(){
     let color =  $(this).css("color")
      if(color == 'rgb(33, 37, 41)'){
        $(this).css("color","red")
      }else{
        $(this).css("color",'rgb(33, 37, 41)')
      }
    })
  },[])
  

let {addProductToWishList} = useContext(WishList);
async function addProductWishList (id){
  let {data} = await addProductToWishList(id)
  if(data.status=="success"){
    toast.success(data.message,{
      duration:2000,
      position:'top-right'
    })
  }else{
    toast.error(data.status,{
      duration:2000
    })
  }
}
let {addToCart,setcartCount} =  useContext(CartContext);
async function addProductTocart (id){
let response =   await addToCart(id)
if(response.data.status=="success"){
  setcartCount(response.data.numOfCartItems)
  toast.success(response.data.message,{
    duration:2000
  })
}else{
  toast.error(response.data.status,{
    duration:2000
  })
}
}

  const baseUrl = 'https://ecommerce.routemisr.com'
  function getFeaturedProduct(){
    return axios.get(`${baseUrl}/api/v1/products`)
  }
  let {data,isLoading} = useQuery('featuredProduct',getFeaturedProduct)
  return (<>
  {isLoading?  <div className="position-fixed top-0 bottom-0 start-0 end-0 d-flex justify-content-center align-items-center">
  <ColorRing
  visible={true}
  height="100"
  width="100"
  ariaLabel="blocks-loading"
  wrapperStyle={{}}
  wrapperClass="blocks-wrapper"
  colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
/>
  </div>: <div className="row py-2 gy-4">
    {data?.data.data.map((product)=>{
      return   <div key={product._id} className="col-md-3">
              <div className="product cursor-pointer p-2">
              <Link to={`/Productdetailes/${product._id}`}>  
        <img src={product.imageCover} className="w-100" alt={product.title} />
        <p className="text-main font-sm mb-0">{product.category.name}</p>
        <h3 className="h6 mb-3">{product.title.split(" ").slice(0,2).join(" ")}</h3>
        <div className="d-flex justify-content-between">
          <span>{product.price}EGP</span>
          <span><i className="fa-solid fa-star rating-color"></i>{product.ratingsAverage}</span>
        </div>
        </Link>
        <i onClick={()=>addProductWishList(product._id)} className="fa-solid fa-heart fa-2x cursor-pointer mt-2"></i>
        <button onClick={()=>addProductTocart(product._id)} className="btn bg-main text-white w-100 btn-sm mt-2">add to cart</button>
      </div>
    </div>
    })}
  </div>}
 </>
  );
}
