import React from "react";
import style from "./CategorySlider.module.css";
import Slider from "react-slick";
import { useQuery } from "react-query";
import axios from "axios";
export default function CategorySlider() {
  const baseUrl = 'https://ecommerce.routemisr.com'
  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 1
  };
function getAllCategory (){
  return axios.get(`${baseUrl}/api/v1/categories`)
}
let {data,isLoading,isError} = useQuery('categorySlider',getAllCategory)
  return (<>
  <h2 className="pt-2 h6">shop Popular Categories</h2>
  {data?.data.data?<Slider  {...settings}>
          {data.data.data.map((category)=>{
      return <img key={category._id} height={200} src={category.image} className="w-100" alt={category.name}/> })}
</Slider>:""} 
    </>
  );
}
