import React, { useContext, useEffect } from "react";
import style from "./productsDetailes.module.css";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query";
import axios from "axios";
import Slider from "react-slick";
import { CartContext } from "../../Context/CartContext";
import toast from "react-hot-toast";
import { WishList } from "../../Context/WishListContext";
import $ from 'jquery'



export default function ProductsDetailes() {
  let {id} =useParams()
  let {addToCart,setcartCount} = useContext(CartContext);
let {addProductToWishList} = useContext(WishList)

useEffect(()=>{
  $('.fa-heart').click(function(){
    $(this).css("color","red")
    console.log("okkkkkk");
  })
},[])
async function addProductWishList (id) {
  let {data} = await addProductToWishList(id)
  if(data.status=='success'){
    toast.success(data.message,{
      duration:2000,
      position:'top-right'
    })
  }else{
    toast.error(data.status)
  }   
}
  async function addProductToCartD (id){
      let response = await addToCart(id)
      if(response.data.status=="success"){
        setcartCount(response.data.numOfCartItems)
        toast.success(response.data.message,{
          duration:2000
        })
      }else{
        toast.error(response.data.status,{
          duration:2000
        })
      }
  }
  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  const BaseUrl = 'https://ecommerce.routemisr.com'

  function getProductdetailes (id){
    return axios.get(`${BaseUrl}/api/v1/products/${id}`)
  }
  let {data,isLoading,isError} = useQuery('productdetailes',()=>getProductdetailes(id))

  return (
    <>
    {data?.data.data?<div className="row align-items-center">
      <div className="col-md-4">
        <Slider className="cursor-pointer" {...settings}>
          {data.data.data.images.map((img)=>{
            return <img key={data.data.data._id} src={img} className="w-100" alt={data.data.data.title} />
          })}
        </Slider>
      </div>
      <div className="col-md-8">
        <h2 className="h5">{data.data.data.title}</h2>
        <p className="text-muted">{data.data.data.description}</p>
        <p className="text-main mb-1">{data.data.data.category.name}</p>
        <div className="d-flex justify-content-between mb-2">
          <span>{data.data.data.price}EGP</span>
          <span><i className="fa-solid fa-star rating-color"></i>{data.data.data.ratingsAverage}</span>
        </div>
        <div className="d-flex justify-content-between">
        <button onClick={()=>addProductToCartD(id)} className="btn bg-main w-75 mx-auto text-white">+add to cart</button>
        <i onClick={()=>addProductWishList(id)} className="fa-solid fa-heart fa-2x cursor-pointer mt-2"></i>

        </div>
      </div>
    </div>  :""}     
    </>
  );
}
