import axios from "axios";
import { createContext, useEffect, useState } from "react";

export let CartContext = createContext()

export default function CartContextProvider (props){
    const baseUrl = 'https://ecommerce.routemisr.com'
let UserToken = localStorage.getItem('userToken')    
let headers = {
    token:UserToken
}
const [cartCount, setcartCount] = useState(0)
useEffect(()=>{
   async function getdata(){
        let  {data} = await getLoggedUserCart()
        if(data?.status=='success'){
            setcartCount(data.numOfCartItems);
        }else if(data?.statusMsg=='fail'){
            setcartCount(0);
        }
    }
    getdata()
},[])
function addToCart(id){
 return  axios.post(`${baseUrl}/api/v1/cart`,
   {
    productId:id
   },
   {
    headers
   }).then((response)=>response)
   .catch((error)=>error)

}
function getLoggedUserCart(){
  return axios.get(`${baseUrl}/api/v1/cart`,{
    headers
  }).then((response)=>response)
  .catch((error)=>error)
}
function deleteSpecificCartItem (id){
    return axios.delete(`${baseUrl}/api/v1/cart/${id}`,{
        headers
    }).then((response)=>response)
    .catch((error)=>error)
}
function clearUserCart (){
    return axios.delete(`${baseUrl}/api/v1/cart`,{
        headers
    }).then((response)=>response)
    .catch((error)=>error)
}
function updataCartProductQuantity (id,count){
    return axios.put(`${baseUrl}/api/v1/cart/${id}`,{
        count
    },{
        headers
    }).then((response)=>response)
    .catch((error)=>error)

}

function checkPayment (id,shippingData){
    let body = {
        shippingAddress:shippingData
    }
    return axios.post(`${baseUrl}/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,body,{
        headers
    })
    .then((response)=>response)
    .catch((error)=>error)
}

    return <CartContext.Provider value={{addToCart,getLoggedUserCart,deleteSpecificCartItem,clearUserCart,updataCartProductQuantity,cartCount,setcartCount,checkPayment}}>
        {props.children} 
    </CartContext.Provider>
} 